    .row.justify-content-center.align-items-center.mb-3(data-aos="zoom-in-left")
      .col-sm-12.col-lg-10
        .cajon.color-acento-botones.p-4.mb-4
            p.mb-0.fw-bold La comunicacin interna tambin debe estar bien organizada, contar con un sistema de comunicacin adecuado para evitar duplicidades de informacin que afecten negativamente la gestin de la calidad.
    p.mb-5(data-aos="zoom-in-left") Para reconocer la gestin en los procesos se debe tener en cuenta el ciclo de #[i Deming], sobre el cual se construyen los sistemas de gestin. En la siguiente figura se aprecia cmo se constituye el ciclo de calidad para la mejora continua.
    .row(data-aos="zoom-in-left")
      .col-sm-12.col-lg-6.mx-auto.mb-5
