          img.w-card-icon(src="@/assets/curso/temas/icon-title.svg")
          h3.py-2.w-card-text Cultura de la calidad
        p.mb-4 Son los principios de calidad que rigen la forma de actuar de las personas que hacen parte de la organizacin, para que interacten entre ellas y as lograr en equipo los objetivos propuestos.
        p Esta base definida en trminos de valores y hbitos, del quehacer diario de todos los que trabajan en la empresa, requiere para su implementacin la capacitacin efectiva y constante, la interaccin a travs de una comunicacin asertiva y eficaz para seguir construyendo una cultura de la calidad slida; la metodologa utilizada son los crculos de calidad desarrollados por Kauro Ishikawa. En la siguiente figura se aprecian sus componentes:
      .col-sm-12.col-lg-3.d-none.d-lg-block
        img.img-a(src='@/assets/curso/temas/tema1/img_23.png')
    
