          img.img-a(src='@/assets/curso/temas/tema1/img_24.svg', alt='En el diagrama del circulo de calidad  se aprecian sus componentes as:  objetivos mejora continua del producto, integrantes son de la misma rea, reuniones acordadas en horario diferente al laboral y en otro ambiente,  participacin voluntaria, equipos de 3 a 15 personas.')
    .d-flex.align-items-center.mt-5.mb-3(data-aos="zoom-in-left")
      img.w-card-icon(src="@/assets/curso/temas/icon-title.svg")
      h3.py-2.w-card-text.mb-cus-8 Metodologa de mejoramiento continuo   
    p.mb-3(data-aos="zoom-in-left") La metodologa se encuentra enmarcada dentro del ciclo de mejoramiento continuo, en el que se desarrollan trece pasos. De manera que todo el personal involucrado en la empresa debe participar para que el mejoramiento sea constante, ya que es un trabajo diario.
    p.mb-5(data-aos="zoom-in-left") Los elementos del Kaizen son:

    .row.justify-content-between.align-items-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-md-6.col-lg-2
        .tarjeta--boton.color-primario.p-4.h-anexo-2.mb-cus
          .row.justify-content-center.mb-3
            .col-sm-2.col-md-6.col-lg-10
                img.img-accord-cus-2(src='@/assets/curso/temas/tema1/img_25.svg')
          h3.text-center.mb-0.text-primario Trabajo en equipo

      .col-sm-12.col-md-6.col-lg-2
        .tarjeta--boton.color-primario.p-4.h-anexo-2.mb-cus
          .row.justify-content-center.mb-3
            .col-sm-2.col-md-6.col-lg-10
                img.img-accord-cus-2(src='@/assets/curso/temas/tema1/img_26.svg')
          h3.text-center.mb-0.text-primario Disciplina personal

      .col-sm-12.col-md-6.col-lg-2
        .tarjeta--boton.color-primario.p-4.h-anexo-2.mb-cus
          .row.justify-content-center.mb-3
            .col-sm-2.col-md-6.col-lg-10
                img.img-accord-cus-2(src='@/assets/curso/temas/tema1/img_27.svg')
          h3.text-center.mb-0.text-primario Moral mejorada

      .col-sm-12.col-md-6.col-lg-2
        .tarjeta--boton.color-primario.p-4.h-anexo-2.mb-cus
          .row.justify-content-center.mb-3
            .col-sm-2.col-md-6.col-lg-10
                img.img-accord-cus-2(src='@/assets/curso/temas/tema1/img_116.svg')
          h3.text-center.mb-0.text-primario Crculos de calidad

      .col-sm-12.col-md-6.col-lg-2
        .tarjeta--boton.color-primario.p-4.h-anexo-2.mb-cus
          .row.justify-content-center.mb-3
            .col-sm-2.col-md-6.col-lg-10
                img.img-accord-cus-2(src='@/assets/curso/temas/tema1/img_29.svg')
          h3.text-center.mb-0.text-primario Sugerencias para las mejoras

    p.mb-3(data-aos="zoom-in-left") Para conocer sobre la metodologa Kaizen, leer la estrategia Kaizen ubicada en las referencias bibliogrficas (Lefcovich, 2009).
    p.mb-5(data-aos="zoom-in-left") La estrategia Kaizen fue desarrollada en Japn una vez terminada la II Guerra Mundial, la cual inicia con el anlisis del problema; en la siguiente figura se pueden consultar los diferentes aspectos a tener en cuenta: 
    .row.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-10.mx-auto
        .titulo-sexto.color-acento-contenido
          h5 Figura 3.
          span
            | Proceso para la solucin de problemas
        img.img-a(src='@/assets/curso/temas/tema1/img_30.svg', alt='La imagen contiene los diferentes aspectos que se deben tener en cuenta para la solucin de problemas ellos son:  Sentido comn, revisin de diseo, trabajo en equipo, mapeo de procesos, habilidades de liderazgo, diseo de experimentos, flujogramas de despliegues, siete nuevas herramientas administrativas, anlisis del rbol de fallas, anlisis de valor e ingeniera de valor,  six sigma, anlisis del modo y efecto de la falla, control estadstico del proceso.')
    p.mb-3(data-aos="zoom-in-left") A continuacin, se presentan los 13 pasos de la metodologa, descritos en la siguiente tabla:
    .row.mb-2(data-aos="zoom-in-left")
      .col-sm-12.col-lg-12
        .titulo-sexto.color-acento-contenido
          h5 Tabla 1.
          span
            | Pasos de la metodologa Kaizen
    .tabla-b.color-acento-contenido.mb-5(data-aos="zoom-in-left")
      .tabla-b__header
        h5.mb-0 Pasos de la metodologa Kaizen
      table
        caption Tabla tomada del SENA - Programa Tcnico control calidad en confeccin industrial (2020).
        tr
          td(rowspan="10").fw-bold.br-bb-td Planear
        tr.bg-tr-odd
          td.br-bl-td.fw-bold.w-col-1 1
          td.br-bl-td.w-col-2 Identificar el rea que requiere mejora.
          td.br-bl-td Utilizar 5S y mapeo de flujo de valor.
        tr.bg-tr-even
          td.fw-bold.br-bl-td.w-col-1 2
          td.br-bl-td  Identificar/clarificar el objetivo Kaizen.
          td.br-bl-td  Mejorar servicio, reducir defectos, mejorar productividad, entre otros.
        tr.bg-tr-odd
          td.fw-bold.br-bl-td.w-col-1  3
          td.br-bl-td  Identificar los miembros del equipo Kaizen.
          td.br-bl-td  Nombrar lder del equipo, miembros de la misma rea y de diferente rea.
        tr.bg-tr-even
          td.fw-bold.br-bl-td.w-col-1 4
          td.br-bl-td  Completar hoja de preparacin del Kaizen.
          td.br-bl-td  Realizar el guion de desarrollo del evento, debe ser aprobado por el lder de rea.
        tr.bg-tr-odd
          td.fw-bold.br-bl-td.w-col-1 5
          td.br-bl-td  Entender la situacin actual.
          td.br-bl-td  Documentar el flujo de informacin, calcular #[i takt time], diagrama espagueti, y documentacin.
        tr.bg-tr-even
          td.fw-bold.br-bl-td.w-col-1 6
          td.br-bl-td  Imaginar una situacin ideal.
          td.br-bl-td  Producir al ritmo #[i takt time], introducir tcnica #[i poka yoke], eliminar tiempos de espera, analizar movimientos.
        tr.bg-tr-odd
          td.fw-bold.br-bl-td.w-col-1 7
          td.br-bl-td  Cuantificar los resultados esperados.
          td.br-bl-td  Determinar mediciones del evento, calcular resultados esperados.
        tr.bg-tr-even
          td.fw-bold.br-bl-td.w-col-1 8
          td.br-bl-td  Desarrollar estrategia Kaizen.
          td.br-bl-td  Identificar obstculos, utilizar simulaciones y pruebas.
        tr.bg-tr-odd
          td.fw-bold.br-bl-td.w-col-1 9
          td.br-bl-td   Desarrollar plan de acciones Kaizen.
          td.br-bl-td  Documentar acciones, identificar recursos requeridos, establecer responsabilidades, fechas y tiempos.
        tr.bg-tr-even
          td(rowspan="4").fw-bold.br-bb-td Hacer
        tr.bg-tr-even
          td.fw-bold.br-bl-td.bt-td.w-col-1 10
          td.br-bl-td.bt-td  Implementar Kaizen.
          td.br-bl-td.bt-td  Desarrollar el evento segn lo planeado, mejorar mtodos, modificar #[i layout], consultar comportamientos, realizar cambios.
        tr.bg-tr-odd
          td.fw-bold.br-bl-td.w-col-1 11
          td.br-bl-td  Verificar los resultados a corto plazo.
          td.br-bl-td  Seguimiento a los colaboradores, verificar progreso, seguimiento al proceso.
        tr.bg-tr-even
          td.fw-bold.br-bl-td.w-col-1 12
          td.br-bl-td  Documentar actividades Kaizen.
          td.br-bl-td  Preparar formatos, registrar informacin, realizar registros fotogrficos.
        tr.bg-tr-odd
          td.fw-bold Verificar<br>Actuar
          td.fw-bold.br-bl-td.bt-td.w-col-1 13
          td.br-bl-td.bt-td  Confirmar los resultados a largo plazo.
          td.br-bl-td.bt-td  Realizar seguimiento con programa (a cargo del responsable del evento), presentar resultados y estandarizar, si no se obtiene el resultado deseado volver a iniciar.


    .d-flex.align-items-center.mt-5.mb-3(data-aos="zoom-in-left")
