      h3.py-2.w-card-text(data-aos="zoom-in-left") #[i Six sigma ]
    p.mb-5(data-aos="zoom-in-left") Es un modelo de gestin, el objetivo es el mejoramiento de los procesos; se conoce por su sigla en ingls DMAIC cuya traduccin est asociada a definir, medir, analizar, mejorar y controlar.
    .row.justify-content-center.mb-5
      .col-sm-12.col-lg-10
        ImagenInfografica.color-secundario.mb-5
          template(v-slot:imagen)
