    p.mb-5(data-aos="zoom-in-left") En cada fase se utilizan herramientas para el anlisis y creacin de grficos como histogramas, diagrama causa-efecto, Pareto, grficos de tendencias, anlisis de correlacin, capacidad del proceso, grficos de control, entre otros.
    .d-flex.align-items-center.mt-5.mb-3(data-aos="zoom-in-left")
      img.w-card-icon(src="@/assets/curso/temas/icon-title.svg")
      h3.py-2.w-card-text Costos de calidad
    p.mb-3(data-aos="zoom-in-left") La meta general de la empresa es ser rentable y obtener ganancias, por eso la relacin de la calidad con el precio se debe analizar, concientizando al personal sobre su importancia.
    p.mb-5(data-aos="zoom-in-left") Es importante entender y concientizar al personal sobre el control de los costos de calidad. En la siguiente figura se puede consultar los tipos de costos de calidad.
    .row.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-9.mx-auto
        .titulo-sexto.color-acento-contenido
          h5 Figura 4.
          span
            | Tipos de costos de calidad
        img.img-a(src='@/assets/curso/temas/tema1/img_32.svg', alt='En el diagrama se  presentan los tipos de costos de calidad:  Costo de  Calidad que comprende la Prevencin y como ejemplo el mantenimiento de mquinas y capacitacin de personal y la Evaluacin como   ejemplo las  inspecciones, pruebas de laboratorio y auditoras internas y externas   y Costos de no calidad que comprenden los fallos internos  como la produccin defectuosa, reprocesos, falda de productividad y paradas de produccin y fallos externos  como devoluciones y demandas.')
    .row.justify-content-center.align-items-center(data-aos="zoom-in-left")
      .col-sm-12.col-lg-8
        .d-flex.align-items-center.mt-5.mb-3(data-aos="zoom-in-left")
          img.w-card-icon(src="@/assets/curso/temas/icon-title.svg")
          h3.py-2.w-card-text-2.mb-cus-10 Sistema de gestin de la calidad, Norma ISO 9001 versin 2015
        p.mb-5 El sistema de gestin de la calidad es una herramienta administrativa, cuenta con una estructura organizada para integrar todos los procesos, e incluye los elementos necesarios para lograr cumplir las necesidades y expectativas del cliente. Obtener la certificacin es evidenciar la capacidad que tiene la empresa para cumplir con los requisitos. Una vez certificada la empresa su vigencia es de tres aos, con auditorias de seguimiento y una vez pasados los tres aos se debe realizar una auditora de renovacin.
        .cajon.color-acento-botones.p-4.mb-4
          p.mb-0.fw-bold La certificacin es una constancia de valor a nivel internacional. El enfoque de la norma est basado en los riesgos, a travs de los cuales la empresa demuestra la gestin de los riesgos y determina las acciones que pueden afectar la capacidad de proporcionar satisfaccin al cliente y obtener los resultados esperados de la empresa.
