    Separador  
    #t_1_3.titulo-segundo.color-primario(data-aos="zoom-in-left")
      h2 1.3 Puntos de control en el proceso de confeccin    
    p.mb-0 A continuacin, se describen los diferentes puntos de control:
    .d-flex.align-items-center.mt-3.mb-3(data-aos="zoom-in-left")
      img.w-card-icon(src="@/assets/curso/temas/icon-title.svg")
      h3.py-2.w-card-text.mb-cus-3 Puntos de control en patronaje
    .row.justify-content-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-9
        .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image':`url(${require('@/assets/curso/temas/tema1/img_36.png')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-4 Se debe establecer diferencia entre los puntos de control y los puntos de control crtico para asegurar la garanta de calidad de la prenda. Una vez se analice el flujo de los procesos mediante los diagramas de rbol y flujogramas, se determina en el proceso y en la prenda, los puntos a inspeccionar segn su relevancia y as se establece la clasificacin.
    p.mb-4(data-aos="zoom-in-left") En el patronaje se debe inspeccionar nmero de piezas, ancho de costuras, medidas de patrn y de prenda terminada. Adems, en las medidas en cortes para unin de piezas, cada lado a unir debe corresponder; se debe disponer del tiempo necesario para realizar la revisin sea en un patrn manual o en el programa especializado.
    p.mb-5(data-aos="zoom-in-left") En el momento de desarrollo del producto se debe realizar la inspeccin, de tal forma que se compare junto con la muestra, y as pueda ser aprobado y pasar a los procesos de produccin.
    .fullview-purple.px-5
      .d-flex.align-items-center.mt-5.mb-3(data-aos="zoom-in-left")
        img.w-card-icon(src="@/assets/curso/temas/icon-title.svg")
        h3.py-2.w-card-text.mb-cus-3 Puntos de control en trazo y corte
      p.mb-3(data-aos="zoom-in-left") Una vez desarrollado el trazo, antes de imprimir en el caso que sea digital, y antes de realizar el extendido de la tela se debe inspeccionar el trazo, revisando la correspondencia de piezas y distribucin de tallas, teniendo en cuenta la orden de produccin, el ancho de trazo y el ancho de tela.
      .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in-left")
        .col-sm-12.col-lg-9
          p.mb-5 El corte se debe revisar una vez se termina de extender. Antes de iniciar el corte de las unidades de capas y alineacin del tendido se debe inspeccionar la apariencia de la tela, tambin se debe tener cuidado con la separacin del corte, sobre todo si se han extendido diferentes rollos de tela en el mismo corte, y verificar el cumplimiento de tiempos de reposo.
          .cajon.color-acento-contenido.p-4
            p.mb-0.fw-bold En la separacin, etiquetado y paqueteo de las piezas, el inspector de calidad debe revisar el corte de las piezas, al comparar con el patronaje de la pieza, contar las piezas del paquete, y verificar el sistema de amarre y rotulado. Es importante disponer de un puesto de trabajo para la inspeccin, con excelente luz y limpieza para determinar defectos en la tela y en el corte de las piezas. Es necesaria la correcta disposicin de las piezas para evitar contaminaciones del paquete con otros colores de estas u otras referencias.
