          p.mb-4 El lote piloto son unidades que se confeccionan con el fin de simular la produccin de una referencia nueva; desarrollado el nuevo producto, es necesario realizar simulaciones para prever fallas en la produccin; la informacin proporcionada una vez se realice el pilotaje brinda confianza sobre la viabilidad del producto y seala las estrategias a efectuar en el momento de la produccin de la prenda.
