                span #[b Organizacin de piezas: ] con el fin de controlar los rendimientos de telas, es importante la disposicin de las piezas, colocando una al lado de la otra y aprovechando lneas o curvas compartidas entre las piezas.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Cantidad de piezas: ] contar el nmero de piezas que corresponda a cada talla del producto.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Ancho de trazo: ] conforme al ancho til de la tela.
      .py-4.py-md-5(titulo="Tendido").bg04
        .row.justify-content-center.align-items-center.mb-5
          .col-sm-12.col-lg-3.d-none.d-lg-block
            img(src='@/assets/curso/temas/tema1/img_58.svg')
          .col-sm-12.col-lg-8.px-sm-cus
            ul.lista-ul--color
              li.color-primario.d-flex
                i.fas.fa-plus
                span  #[b Alineacin de capas: ] respetar el margen colocado a partir de la base, es decir, la primera capa. Tambin en los extremos debe alcanzar a cubrir el largo del trazo, sobre todo en trazos escalonados.
              li.color-primario.d-flex
                i.fas.fa-plus
                span  #[b Uniformidad de tendido: ] el extendido de cada capa no debe tener arrugas.
              li.color-primario.d-flex
                i.fas.fa-plus
                span  #[b Cantidad de capas: ] antes de cortar se debe contar el nmero de capas para que correspondan a las unidades a cortar, segn la distribucin del trazo.
              li.color-primario.d-flex
                i.fas.fa-plus
                span  #[b Tiempos de reposo: ] sobre todo en tejidos de punto para evitar encogimiento, se tiene en cuenta la ficha tcnica de la tela y se valida con pruebas que confirmen niveles de encogimiento, que se desarrollan una vez finalicen los tiempos de reposo.
              li.color-primario.d-flex
                i.fas.fa-plus
                span  #[b Lado de tela: ] a partir del tipo de trazo se debe respetar el derecho de la tela.
              li.color-primario.d-flex
                i.fas.fa-plus
                span  #[b Tela: ] defectos de tela, marcas, piques, marras, entre otras. Cuidar la combinacin de rollos y de colores para evitar contaminacin entre los colores, debido a los residuos de las fibras en el corte del tejido.
    
