              li.color-primario.d-flex
                i.fas.fa-plus
                span  #[b Descripcin: ] la informacin debe corresponder a cada pieza, contener el nmero consecutivo de la misma en orden, segn el tendido, adems debe incluir talla y referencia.
      .py-4.py-md-5(titulo="Fusionado").bg04
        .row.justify-content-center.align-items-center.mb-5
