    #t_1_6.titulo-segundo.color-primario(data-aos="zoom-in-left")
      h2 1.6 Control de calidad en confeccin    
    p.mb-5(data-aos="zoom-in-left") Tiene en cuenta la distribucin de las operaciones, es decir, la hoja de ruta; operaciones de preparacin, ensamble y terminados, cada proceso tiene un mtodo, un tiempo estndar, descripcin de la maquinaria, pies, guas, folderes y aditamentos que se deben revisar.
    .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-9
        p.mb-5 En los puntos de revisin se realizan inspecciones, en las que se retienen prendas o piezas con defectos para analizar las causas y determinar acciones, as mismo, la revisin se lleva a cabo en los puestos de trabajo, donde la responsabilidad es de cada operario con el acompaamiento y apoyo del auditor de calidad en pie de mquina. Conforme corresponda la inspeccin se utilizan los formatos de producto terminado y en proceso.
        .cajon.color-acento-contenido.p-4
          p.mb-0.fw-bold Otros factores para tener en cuenta adems de los anteriormente mencionados que son directos al proceso, son los factores ambientales como la iluminacin, ventilacin, temperatura, ruido, espacio, aseo, entre otros; y tambin los relacionados con el recurso humano como la capacitacin del personal y las relaciones interpersonales, aspectos que inciden en los resultados de los productos y por ende, en la calidad de estos.
      .col-sm-12.col-lg-3
        img.d-none.d-lg-block(src='@/assets/curso/temas/tema1/img_63.png')
    p.mb-5(data-aos="zoom-in-left") Tambin se pueden realizar pruebas o ensayos en laboratorios especializados que evalen la prenda segn normas nacionales e internacionales, una de ellas es la prueba de uso, en la que se compara los resultados planteados frente a los obtenidos.
    .row.justify-content-center.mb-5(data-aos="zoom-in-left")
