      h3.py-2.w-card-text.mb-cus-3 Requisitos de calidad en confeccin
    p.mb-5 Los requisitos que se deben cumplir en la confeccin y que son objetos de control son:
    .row.justify-content-around.align-items-center.mb-5
      .col-sm-12.col-lg-3.d-none.d-lg-block
        img(src="@/assets/curso/temas/tema1/img_65.svg")
      .col-sm-12.col-lg-9
        .row.justify-content-between.align-items-center
          .col.mb-3.mb-sm-0
            ul.lista-ul--color
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Costura:] simetra, sin recogidos ni prenses, costuras rectas, respetar mrgenes de costura, costura uniforme, marquilla de acuerdo con el cuidado de la prenda, remates uniformes, clase de costuras.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Tela:] con colores uniformes, tela sin arrugas, sin imperfecciones.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Ambiente del taller:] orden y limpieza en piso, puestos de trabajo.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Ajuste de maquinaria:] mantenimiento de la mquina y uso de aditamentos, pies, guas y folderes, agujas, altura de dientes, presin del pie prnsatela, enhebrado, devanado de hilo, tensin del hilo.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Operario:] manipulacin del material, mtodos utilizados en la operacin.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Instrucciones de cuidado y terminados:] segn la norma, tener en cuenta los smbolos y la composicin del material, incluidas en la etiqueta adherida en la prenda.
    .row.justify-content-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-8
        .tarjeta.color-primario.p-3
