            .col-sm-12.col-lg-2.d-none.d-lg-block
              img.w-75(src="@/assets/curso/temas/tema1/img_104.svg")
            .col-sm-12.col-lg-10
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p.mb-0 A continuacin, se presentan imgenes descriptivas de los defectos que pueden existir en las costuras, textiles e insumos:
   
   
 
