    Separador  
    #t_1_7.titulo-segundo.color-primario(data-aos="zoom-in-left")
      h2 1.7 Control de calidad en terminados   
    .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-9
        p.mb-3 En los terminados se incluyen procesos de planchado, empaque, embalaje y procesos de acabados como lavandera, estampacin y bordado.
        p.mb-5 Puede ser que los procesos de acabados se desarrollen de manera externa a la empresa, es decir, contratando los servicios, por ello la importancia de los puntos de control cuando ingresan a la planta; se deben validar las prendas o piezas una vez se realicen los procesos.
        .cajon.color-acento-contenido.p-4
          p.mb-0.fw-bold El planchado es la ltima operacin del proceso de produccin, es necesario que las personas responsables de esta seccin tengan los criterios claros para evaluar las prendas que pasarn a esta operacin, para la posterior entrega al cliente.
      .col-sm-12.col-lg-3
        img.d-none.d-lg-block(src='@/assets/curso/temas/tema1/img_94.png') 
    .d-flex.align-items-center.mb-2(data-aos="zoom-in-left")
      img.w-card-icon(src="@/assets/curso/temas/icon-title.svg")
      h3.py-2.w-card-text.mb-cus-3 Requisitos de calidad en acabados
    p.mb-5(data-aos="zoom-in-left") Los requisitos a revisar en cada una de las actividades del proceso de acabados son:
    .row.justify-content-around.align-items-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-3.d-none.d-lg-block
        img(src="@/assets/curso/temas/tema1/img_95.svg")
      .col-sm-12.col-lg-9
        .row.justify-content-between.align-items-center
          .col.mb-3.mb-sm-0
            ul.lista-ul--color
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Planchado:] uniforme, quiebres segn lo especificado, si se debe doblar que no genere arrugas, control en la temperatura para que no queme, limpieza, adems del control en la presin y la humedad.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Empaque:] segn lo establecido por la empresa, utilizar los recursos como el gancho, bolsa y etiqueta correspondiente.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Embalaje:] tener en cuenta el pedido del cliente, utilizando materiales como cajas, cinta de amarre o zuncho, cinta adhesiva, tensor para la cinta de amarre. Rotular para identificar el contenido, organizar el contenido.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Estampados:] uniformidad del dibujo y colores, posicin y ubicacin.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Bordados:] tensin del hilo, colores y letras que correspondan al diseo, posicin y ubicacin.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Rebordados:] alineacin y ubicacin al colocar pedrera u otros; el diseo debe corresponder al rebordado realizado.
              li.color-primario.d-flex
                i.fas.fa-plus
                span #[b Lavandera:] uniformidad de colores, que el proceso corresponda al solicitado, sin daar costuras o tejido de la tela.

    Separador  
    #t_1_8.titulo-segundo.color-primario(data-aos="zoom-in-left")
      h2 1.8 Control de calidad en insumos, materiales y textiles    
    p.mb-5(data-aos="zoom-in-left") La comunicacin con la cadena de abastecimiento es muy importante para solucionar de manera eficiente los problemas derivados que puedan ocurrir en la produccin de las prendas.
    .row.justify-content-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-9
        .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image':`url(${require('@/assets/curso/temas/tema1/img_96.png')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-0 Los espacios disponibles para recibir los diferentes elementos deben cumplir condiciones ptimas de ventilacin, iluminacin, seguridad, aseo y organizacin, ya que esto puede afectar los requerimientos de calidad en la cadena productiva. El control de inventarios est relacionado directamente con el control de calidad, para generar mejoras en toda la gestin de la empresa.
    .row.justify-content-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-9
        .cajon.color-acento-contenido.p-4
          p.mb-0.fw-bold.px-4 En las entradas del proceso de produccin uno de los recursos son los insumos y materias primas, las revisiones respectivas responden a uno de los principios de calidad, que es hacer las cosas bien desde el principio, asegurando el flujo de los procesos, as mismo, al finalizar las operaciones de produccin se requieren materiales para la entrega del producto al cliente, los cuales de igual manera se deben validar.
    .d-flex.align-items-center.mb-2(data-aos="zoom-in-left")
      img.w-card-icon(src="@/assets/curso/temas/icon-title.svg")
      h3.py-2.w-card-text.mb-cus-3 Control de calidad en insumos
    p.mb-5(data-aos="zoom-in-left") Los requisitos necesarios para cumplir con las solicitudes de insumos son:
    .tarjeta--container.row.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-md-6.col-lg-6.tarjeta.color-primario.p-5.h-card-ctl
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/temas/tema1/img_97.png', alt='Texto que describa la imagen')
        h2.text-center Disponibilidad
        p.mb-0.text-center El tiempo de obtencin para su uso
      .col-sm-12.col-md-6.col-lg-6.tarjeta.color-acento-botones.p-5.h-card-ctl
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/temas/tema1/img_98.png', alt='Texto que describa la imagen')
        h2.text-center Caractersticas propias
