    p.mb-5(data-aos="zoom-in-left") En los casos de hilos se analizan propiedades como elasticidad, flexibilidad, durabilidad, resistencia a rotura, tenacidad, solidez al color, uniformidad de tono y grosor. En los elsticos es importante realizar pruebas de recuperacin de su longitud, teniendo en cuenta el reposo y la tensin ejercida en su elongacin.
